.modal {
	background-color: $light;
	max-width: 1200px;
	width: 100%;
	margin-left: auto;
	margin-right: auto;
	padding: 40px 60px;
	position: relative;
	border-radius: 10px;
	@include r(1200) {
		padding: 20px 30px;
	}
	@include r(480) {
		padding: 10px 10px;
	}
	.mfp-close {
		color: $accent;
		transition: 0.3s all;
		opacity: 1;
		&:hover {
			opacity: 0.7;
		}
	}
}